"use client";

import { useState, useEffect } from 'react';
import { getDatabase, ref, onValue } from "firebase/database";
import app from '../lib/firebase';

const ConnectionStatus = () => {
  const [isConnected, setIsConnected] = useState(false);

  useEffect(() => {
    // Asegúrate de que este código solo se ejecute en el lado del cliente
    if (typeof window !== "undefined") {
      const db = getDatabase(app);
      const connectedRef = ref(db, ".info/connected");
      const unsubscribe = onValue(connectedRef, (snap) => {
        const status = snap.val() === true;
        setIsConnected(status);
        console.log("Firebase connection status:", status ? "Connected" : "Disconnected");
      });

      return () => {
        unsubscribe();
      };
    }
  }, []);

  return (
    <div className="fixed bottom-4 right-4 z-50">
      <div className={`px-4 py-2 rounded-full text-white shadow-lg text-sm font-semibold transition-colors duration-300 ${isConnected ? 'bg-green-500' : 'bg-red-500'}`}>
        {isConnected ? 'Conectado a Firebase' : 'Desconectado'}
      </div>
    </div>
  );
};

export default ConnectionStatus;
